@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #f9fbfd;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-header {
  min-height: 758px;
  background-color: #1667e0;
  background-image: image-set("../img/main-bg-desktop.webp" 1x, "../img/main-bg-desktop@2x.webp" 2x, "../img/main-bg-desktop.jpg" 1x, "../img/main-bg-desktop.jpg" 2x);
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 1023px) {
  .main-header {
    min-height: 638px;
    background-image: image-set("../img/main-bg-tablet.webp" 1x, "../img/main-bg-tablet@2x.webp" 2x, "../img/main-bg-tablet.jpg" 1x, "../img/main-bg-tablet.jpg" 2x);
  }
}
@media (max-width: 767px) {
  .main-header {
    min-height: 500px;
    background-image: image-set("../img/main-bg-mobile.webp" 1x, "../img/main-bg-mobile@2x.webp" 2x, "../img/main-bg-mobile.jpg" 1x, "../img/main-bg-mobile.jpg" 2x);
  }
}

.container {
  max-width: 1260px;
  margin: 0 auto;
}
.container--main-header {
  display: grid;
  grid-template-columns: 1fr 60%;
  align-items: center;
  padding: 35px 50px;
}
@media (max-width: 1150px) {
  .container--main-header {
    grid-template-columns: 1fr 66%;
  }
}
@media (max-width: 1023px) {
  .container--main-header {
    grid-template-columns: 1fr 74%;
    padding: 35px 45px;
  }
}
@media (max-width: 767px) {
  .container--main-header {
    padding: 20px 15px 30px;
  }
}
.container--main-block {
  padding: 213px 145px 100px;
}
@media (max-width: 1023px) {
  .container--main-block {
    padding: 158px 45px 70px;
  }
}
@media (max-width: 767px) {
  .container--main-block {
    padding: 66px 15px 0;
  }
}
.container--promo-block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 88px;
}
@media (max-width: 1023px) {
  .container--promo-block {
    gap: 25px;
  }
}
@media (max-width: 767px) {
  .container--promo-block {
    grid-template-columns: 1fr;
    gap: 50px;
  }
}
.container--main-footer {
  display: grid;
  grid-template-columns: 70px 1fr 106px;
  align-items: center;
  gap: 40px;
  padding: 50px;
}
@media (max-width: 1023px) {
  .container--main-footer {
    grid-template-columns: 38px 1fr 87px;
    gap: 25px;
    padding: 50px 45px;
  }
}
@media (max-width: 767px) {
  .container--main-footer {
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 40px 15px;
  }
}

@media (max-width: 767px) {
  .menu--closed .navigation {
    display: none;
  }
  .menu--closed .navigation__toggle {
    position: absolute;
    right: 22px;
    top: 24px;
    z-index: 2;
    display: block;
    width: 24px;
    height: 12px;
    padding: 0;
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }
  .menu--closed .navigation__toggle-bar {
    display: block;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
  }
  .menu--closed .navigation__toggle-bar::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
  }
  .menu--closed .navigation__toggle-bar::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .menu--opened .navigation {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 90%;
    min-height: 500px;
    padding: 85px 25px 25px;
    background-color: #f9fbfd;
  }
  .menu--opened .main-header__logo {
    position: absolute;
    top: 20px;
    left: 15%;
    z-index: 2;
    fill: #011c40;
  }
  .menu--opened .logo__icon {
    fill: #011c40;
  }
  .menu--opened .navigation__toggle {
    position: absolute;
    right: 22px;
    top: 18px;
    z-index: 2;
    display: block;
    width: 13px;
    height: 13px;
    padding: 11px;
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }
  .menu--opened .navigation__toggle-bar::before, .menu--opened .navigation__toggle-bar::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 3px;
    width: 16px;
    height: 2px;
    background-color: #011c40;
  }
  .menu--opened .navigation__toggle-bar::before {
    transform: rotate(45deg);
  }
  .menu--opened .navigation__toggle-bar::after {
    transform: rotate(-45deg);
  }
  .menu--opened .navigation__link {
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .menu--nojs .main-header__logo {
    position: static;
    align-self: start;
    fill: #011c40;
  }
  .menu--nojs .navigation__toggle {
    display: none;
  }
  .menu--nojs .navigation {
    display: block;
    min-height: auto;
    padding: 0;
  }
  .menu--nojs .navigation__list {
    gap: 8px;
  }
  .menu--nojs .navigation__link {
    font-size: 14px;
    line-height: 17px;
    color: #f9fbfd;
  }
}

.main-block__title {
  margin: 0 0 30px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-block__title {
    margin: 0 0 29px;
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .main-block__title {
    margin: 0 0 25px;
    font-size: 36px;
    line-height: 45px;
  }
}

.main-block__description {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-block__description {
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .main-block__description {
    font-size: 16px;
    line-height: 22px;
  }
}

.logo__icon {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .logo__icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__icon {
    width: 42px;
    height: 22px;
  }
}

.navigation__toggle {
  display: none;
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
  gap: 33px;
}
@media (max-width: 1023px) {
  .navigation__list {
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .navigation__list {
    flex-direction: column;
  }
}

.navigation__link {
  font-weight: 500;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .navigation__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .navigation__link {
    gap: 26px;
    font-size: 18px;
    line-height: 22px;
  }
}

.promo-block {
  padding: 120px 50px 145px;
}
@media (max-width: 1023px) {
  .promo-block {
    padding: 100px 45px;
  }
}
@media (max-width: 767px) {
  .promo-block {
    padding: 65px 15px 85px;
  }
}

.promo-block__title {
  margin: 0 0 40px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: #011c40;
}
.promo-block__title br {
  display: none;
}
@media (max-width: 1023px) {
  .promo-block__title {
    margin: 0 0 30px;
    font-size: 26px;
    line-height: 36px;
  }
  .promo-block__title br {
    display: block;
  }
}
@media (max-width: 767px) {
  .promo-block__title {
    font-size: 24px;
    line-height: 34px;
  }
}

.promo-block__description {
  margin: 0 0 40px;
  line-height: 24px;
  color: #011c40;
}
@media (max-width: 767px) {
  .promo-block__description {
    margin: 0;
  }
